import React from 'react';

import Film from './../Film/Film';



const Films = ({ films, openModal, closeModal, isVisible }) => {

	const filmsList = films.map((film, i) =>{
		return (<div className="ba b--yellow film">
					<Film film={{film}} 
					openModal={openModal} 
					closeModal={closeModal}
					isVisible={isVisible}/>
				</div>
	);
	})

	return(
		<div>
			{filmsList}
		</div>
		);
}
export default Films;